.map-wrapper {
    height: 100vh;
    position: relative;
}

#map {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
}

#map {
    width: 100%;
}


#features {
    height: 100%;
    width: 50%;
    margin-left: 50%;
    font-family: sans-serif;
    overflow-y: scroll;
    background-color: transparent;
}

.map-chapter {
    padding-top: 50vh;
    padding-bottom: 50vh;
    padding-left: 5%;
    padding-right: 5%;
    line-height: 25px;
    opacity: 0.99;

}

.scroll-notification {
    position: sticky;
    color: black;
    bottom: 0px;
}

.map-chapter p {
    padding: 20px;
    text-align: left;
    font-size: 15px;
    line-height: 140%;
    color: black;
    background-color: #ffffff;
    opacity: 0.9;
    filter: alpha(opacity=90);
}

.map-chapter.active {
}

.map-chapter:last-child {
    border-bottom: none;
    margin-bottom: 200px;
}