.scroll-cta {
  cursor: pointer;
  padding: 10px 0;
  margin: 5% auto;
  position: relative;
  text-align: center;
  width: 15%;
  &:before {
    content: "";
    border-top: 4px solid #e63520;
    display: block;
    width: 12%;
    text-align: center;
    margin: 0 auto;
  }
}

.scroll-cta__link {
  color: #fff;
  font-size: 12px;
  text-decoration: none;
  text-transform: uppercase;
}
.scroll-cta__link:after {
  animation: bounce 2s infinite ease-in-out;
  background: url(data:image/svg+xml;base64,) no-repeat center center/100%;
  color: #fff;
  content: "";
  display: block;
  margin: 0 auto;
  padding: 1em 0;
  height: 35px;
  width: 35px;
}
